import { push } from 'connected-react-router';
import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { generatePath, Redirect } from 'react-router-dom';
import useLoggedInUser from '../../hooks/useLoggedInUser';
import { login } from '../../redux/auth/action';
import routesMap from '../../utils/routesMap';
import './login.css'

interface Props {
  children: React.ReactElement;
}

function RedirectLogin(): JSX.Element {
  const dispatch = useDispatch();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  function doLogin () {
    if(username === '' || password === ''){
      alert('Không được để trống tài khoản hoặc mật khẩu!')
      return
    }
    const payload = {
      username: username,
      password: password
    }
    dispatch(
      login(payload,
        (data: any) => {
          dispatch(
            push(
              generatePath(routesMap.HOME),
            ),
          );
        },
        (message: string) => {
          alert(message)
        }
      )
    )
  }

  function onKeyPress(e: React.KeyboardEvent<HTMLDivElement | HTMLInputElement>) {
    if(e.key === "Enter"){
      doLogin()
    }
  }

  useEffect((): void => {
    // dispatch(login());
  }, [dispatch]);

  return <div className="outer">
    <div className="inner">
      <div>
      <h3>Log in</h3>

      <div className="form-group">
        <label>Tài khoản</label>
        <input type="text" className="form-control" placeholder="Nhập tài khoản" value={username} onKeyPress={onKeyPress} onChange={(e) => {setUsername(e.target.value)}}/>
      </div>

      <div className="form-group">
        <label>Mật khẩu</label>
        <input type="password" className="form-control" placeholder="Nhập mật khẩu" value={password} onKeyPress={onKeyPress} onChange={(e) => {setPassword(e.target.value)}} />
      </div>

      <div className="form-group">
        <div className="custom-control custom-checkbox">
          <input type="checkbox" className="custom-control-input" id="customCheck1" />
          <label className="custom-control-label" htmlFor="customCheck1">Remember me</label>
        </div>
      </div>

      <button onClick={doLogin} className="btn btn-dark btn-lg btn-block">Đăng nhập</button>
      {/* <p className="forgot-password text-right">
        Forgot <a href="#">password?</a>
      </p> */}
    </div>
    </div>
  </div>;
}

const Login: React.FC<Props> = (): JSX.Element => {
  const { isLoggedIn } = useLoggedInUser();
  
  if (isLoggedIn) {
    return <Redirect to={routesMap.HOME} />;
  }

  return <RedirectLogin />;
};

export default Login;
